
import XHeader from 'vux/src/components/x-header/index.vue'

import dayuan from "../../../common/js/dayuan.js";
import Progress from "@/components/progress";
export default {
filters: {
schedule(count, total) {
if (!count) {
return 0;
} else {
return Number.parseFloat(((count / total) * 100).toFixed(2));
}
},
imgUrl(id) {
return dayuan.aImg(id);
}
},
data() {
return {
Total: 0,
List: [],
params: {}
};
},
components: {
XHeader,
Progress
},
mounted() {
this.getList();
},
methods: {
getList() {
this.$vux.loading.show({
text: "加载中"
});
let params = this.$route.params.perxObj;
this.params = { ...params };
console.log(this.$route, "params");
params["endCostDate"] = params["endDendCostDateate"];
params["endDendCostDateate"] = null;
const url = `CostStatis/GetCostClassType`;
axios.get(url, { params }).then(res => {
const data = res.data.Data;
this.List = data;
this.Total = data.map(v => v.Value).reduce((prev, next) => prev + next);
this.$vux.loading.hide();
});
},
toDelate(type, id) {
let params = { ...this.params, mode: type, classID: id };
this.$router.push({ name: "cost", params: { perxObj: params } });
}
}
};



